.hungry-flamingo-wrapper {padding:0;}
.hungry_flamingo_admin_header {background-color:#ff00ff; padding:1em; border-radius:23px;margin-bottom:2em;}
.hungry_flamingo_admin_header img{width:100%; max-width:223px;}
.hungry_flamingo_admin_header h2{padding-top:1em}
.hungry_flamingo_admin_header div.postbox-header{background-color:#ff00ff}
.hungry_flamingo_admin_header div.postbox-header div.handle-actions button{color:#f3f3f3}
.hungry_flamingo_admin_header h2 span{background-color:#ff00ff;color:#fff;font-weight:700;padding:0 1em;text-transform:uppercase}
#hungry_flamingo_admin_footer{margin-top:4em}
.hungry-flamingo-notice{margin:5px 20px 2px 2px}
.hungry_flamingo_admin_pb_description{background-color:#fff;border:1px solid #ccc;border-radius:5px;margin-right:20px;padding:1em}
.hungry_flamingo_admin_helptip span.dashicons{font-size:18px;margin:0 5px;vertical-align:middle}
.hungry_flamingo_admin_unit{margin-left:3px}
.hungry_flamingo_helptip_element,.hungry_flamingo_helptip_element_depr{border-radius:5px;box-shadow:0 0 8px 0 rgba(0,0,0,.2);color:#fff!important}
.hungry_flamingo_helptip_element,.hungry_flamingo_helptip_element_depr,.hungry_flamingo_helptip_element_depr div,.hungry_flamingo_helptip_element div{background-color:#333!important;background:#333!important;font-family:-apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Oxygen-Sans,Ubuntu,Cantarell,Helvetica Neue,sans-serif;font-size:11px}
.hungry_flamingo_helptip_element_depr div,.hungry_flamingo_helptip_element div{color:#fff}
#hungry_flamingo_meta_box h2.ui-sortable-handle span:before{content:url(data:image/svg+xml;base64,);display:inline-block;margin:0 7px 0 0;width:11px}#hungry_flamingo_meta_box .hungry_flamingo_admin_metabox_customerinsights span{display:block}#hungry_flamingo_meta_box .hungry_flamingo_admin_metabox_customerinsights .hungry_flamingo_admin_helptip{display:inline-block;font-size:11px;margin-left:3px}.hungry_flamingo_admin_section p.submit{display:inline-block;margin-left:1em}.hungry-flamingo-wrapper .disabled{filter:Alpha(opacity=50);opacity:.5}.hungry_flamingo_admin_section #success{color:#14b084;display:inline-block;margin-left:1em}#hungry_flamingo_logged_in_info{color:#14b084}#hungry_flamingo_privacy_email{font-size:80%;margin-bottom:-1em;margin-top:1em}.hungry_flamingo_logout{cursor:pointer;text-decoration:underline}.hungry_flamingo_admin_section_login .hidden{display:none!important}ul.hungry_flamingo_admin_features{list-style:square inside}ul.hungry_flamingo_admin_features ul{margin-left:1.2em;margin-top:.4em}#hungry_flamingo_logged_in_required{background-color:#f3f3f3;border:3px dashed red;border-radius:5px;font-weight:700;margin:2em .5em;padding:1em}#hungry_flamingo_admin_widget a{text-decoration:none!important}#hungry_flamingo_admin_widget div.postbox-header,#hungry_flamingo_admin_widget h2{background-color:#14b084!important;color:#fff!important}#hungry_flamingo_admin_widget h2{font-weight:700!important;text-transform:uppercase!important}#hungry_flamingo_admin_widget .order-higher-indicator,#hungry_flamingo_admin_widget .order-lower-indicator,#hungry_flamingo_admin_widget .toggle-indicator{color:#fff!important}#hungry_flamingo_admin_widget .inside{padding:0!important}.hungry_flamingo_admin_dashboard_widget{padding:0 12px 12px}.hungry_flamingo_admin_dashboard_widget h3{border-top:1px solid #f3f3f3;font-weight:700!important;padding-top:1em!important}.hungry_flamingo_admin_dashboard_widget .hungry_flamingo_admin_tag{background-color:#14b084;border-radius:3px;color:#fff;font-size:75%;margin-right:.5em;padding:.2em .5em;text-transform:uppercase}.hungry_flamingo_admin_dashboard_widget small{font-style:italic;font-weight:75%}#hungry_flamingo_admin_dashboard_news p{margin:.3em 0;padding-bottom:1em}.hungry_flamingo_admin_dashboard_widget_footer{border-top:1px solid #f3f3f3;padding:0 12px}.hungry_flamingo_admin_dashboard_widget_footer li{border-left:1px solid #f3f3f3;display:inline-block;margin-bottom:0;padding:0 .7em}.hungry_flamingo_admin_dashboard_widget_footer li:first-child{border:0!important;display:inline-block;margin-bottom:0;padding:0 .3em 0 0}


